import {createStore} from "redux";
import rootReducer from "../reducers/index";

export default function configStore(initialState) {
	return createStore(rootReducer,
		initialState,
		window.__REDUX_DEVTOOLS_EXTENSION__
		&& window.__REDUX_DEVTOOLS_EXTENSION__());
}



